.aboutContainer {
  color: #fff;
  width: 85%;
  height: 93%;
  overflow: scroll;
  padding: 20px;
}
.aboutTop {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.3vw;
}
.aboutNameContainer {
  display: -ms-flexbox;
  display: flex;
}

.aboutName {
  font-size: 4vw;
  text-align: left;
  margin: 8px;
  padding: 0;
}

.aboutStatement {
  text-align: center;
}

.aboutImg {
  max-height: 20%;
  max-width: 20%;
  /* position: absolute;
  right: 0;
  top: 0; */
  border-radius: 50%;
  /* animation: roll-in 2s ease;
  animation-timing-function: linear; */
}

@-webkit-keyframes roll-in {
  0% {
    -webkit-transform: translateX(400%);
            transform: translateX(400%);
  }
  25% {
    -webkit-transform: translateX(300%) rotate(-90deg);
            transform: translateX(300%) rotate(-90deg);
  }
  50% {
    -webkit-transform: translateX(200%) rotate(-180deg);
            transform: translateX(200%) rotate(-180deg);
  }
  75% {
    -webkit-transform: translateX(100%) rotate(-270deg);
            transform: translateX(100%) rotate(-270deg);
  }
  100% {
    -webkit-transform: translateX(0) rotate(-360deg);
            transform: translateX(0) rotate(-360deg);
  }
}

@keyframes roll-in {
  0% {
    -webkit-transform: translateX(400%);
            transform: translateX(400%);
  }
  25% {
    -webkit-transform: translateX(300%) rotate(-90deg);
            transform: translateX(300%) rotate(-90deg);
  }
  50% {
    -webkit-transform: translateX(200%) rotate(-180deg);
            transform: translateX(200%) rotate(-180deg);
  }
  75% {
    -webkit-transform: translateX(100%) rotate(-270deg);
            transform: translateX(100%) rotate(-270deg);
  }
  100% {
    -webkit-transform: translateX(0) rotate(-360deg);
            transform: translateX(0) rotate(-360deg);
  }
}



